import React, { useState } from "react";
// @material-ui/core components
import { makeStyles, withStyles } from "@material-ui/core/styles";
import { useTheme } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import InputBase from "@material-ui/core/InputBase";
import Button from "../elements/Button";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import Container from "@material-ui/core/Container";
import Divider from "@material-ui/core/Divider";
import FilledInput from "@material-ui/core/FilledInput";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import FormLabel from "@material-ui/core/FormLabel";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import NativeSelect from "@material-ui/core/NativeSelect";
import Input from "./../elements/Input";
import FormHint from "../elements/FormHint";
import axios from "axios";
import Modal from "../elements/Modal";
import AlertDialog from "../elements/MaterialModal";
import validator from "validator";
import {
  CircularProgress,
  Select,
  InputLabel,
  MenuItem,
} from "@material-ui/core";
import "./Register.css";

// core components

import componentStyles from "./../../assets/theme/Register";

const useStyles = makeStyles(componentStyles);
const BootstrapInput = withStyles((theme) => ({
  root: {
    "label + &": {
      marginTop: theme.spacing(3),
    },
  },
  input: {
    borderRadius: 4,
    position: "relative",
    backgroundColor: theme.palette.background.paper,
    border: "1px solid #ced4da",
    fontSize: 16,
    padding: "10px 26px 10px 12px",
    transition: theme.transitions.create(["border-color", "box-shadow"]),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:focus": {
      borderRadius: 4,
      borderColor: "#80bdff",
      boxShadow: "0 0 0 0.2rem rgba(0,123,255,.25)",
    },
  },
}))(InputBase);

const workshops = [
  {
    name: "Machine Learning with Python",
    content: "11th,Sept 2021",
    delay: "200",
    image: "ML.jpg",
    // gForm : ""
  },
  {
    name: "Full Stack Web Development",
    content: "11th,Sept 2021",
    delay: "400",
    image: "web.jpg",
    // gForm : ""
  },
  {
    name: "Product & UI/UX design",
    content: "11th,Sept 2021",
    delay: "600",
    image: "ui.jpg",
    // gForm : "http://tinyurl.com/techvantoacademyworkshop"
  },

  // {
  //   name: "Automobile",
  //   content: "11th,Sept 2021",
  //   delay: "600",
  //   image: "Automobile.jpg",
  //   // gForm : "http://tinyurl.com/techvantoacademyworkshop"
  // },
  {
    name: "Embedded Systems with Robotics & IoT",
    content: "11th,Sept 2021",
    delay: "200",
    image: "embd.jpg",
    // gForm : "http://tinyurl.com/techvantoacademyworkshop"
  },
  {
    name: "Cyber Security and Ethical Hacking",
    content: "11th,Sept 2021",
    delay: "400",
    image: "cyber.jpg",
    // gForm : "http://tinyurl.com/techvantoacademyworkshop"
  },
  {
    name: "Mechatronics with Advanced Study",
    content: "11th,Sept 2021",
    delay: "600",
    image: "mechatronics.jpg",
    // gForm : ""
  },
  {
    name: "CPM (Construction Project Management)",
    content: "11th,Sept 2021",
    delay: "200",
    image: "cpm.jpg",
    // gForm : "http://tinyurl.com/techvantoacademyworkshop"
  },
  // {
  //   name: "Ethical Hacking",
  //   content: "11th,Sept 2021",
  //   delay: "600",
  //   image: "ethical.jpg",
  //   // gForm : "http://tinyurl.com/techvantoacademyworkshop"
  // },
];

function Register({ workshop, handleClose }) {
  const workshops = [
    {
      name: "Machine Learning with Python",
      content: "11th,Sept 2021",
      delay: "200",
      image: "ML.jpg",
      // gForm : ""
    },
    {
      name: "Full Stack Web Development",
      content: "11th,Sept 2021",
      delay: "400",
      image: "web.jpg",
      // gForm : ""
    },
    {
      name: "Product & UI/UX design",
      content: "11th,Sept 2021",
      delay: "600",
      image: "ui.jpg",
      // gForm : "http://tinyurl.com/techvantoacademyworkshop"
    },

    // {
    //   name: "Automobile",
    //   content: "11th,Sept 2021",
    //   delay: "600",
    //   image: "Automobile.jpg",
    //   // gForm : "http://tinyurl.com/techvantoacademyworkshop"
    // },
    {
      name: "Embedded Systems with Robotics & IoT",
      content: "11th,Sept 2021",
      delay: "200",
      image: "embd.jpg",
      // gForm : "http://tinyurl.com/techvantoacademyworkshop"
    },
    {
      name: "Cyber Security and Ethical Hacking",
      content: "11th,Sept 2021",
      delay: "400",
      image: "cyber.jpg",
      // gForm : "http://tinyurl.com/techvantoacademyworkshop"
    },
    {
      name: "Mechatronics with Advanced Study",
      content: "11th,Sept 2021",
      delay: "600",
      image: "mechatronics.jpg",
      // gForm : ""
    },
    {
      name: "CPM (Construction Project Management)",
      content: "11th,Sept 2021",
      delay: "200",
      image: "cpm.jpg",
      // gForm : "http://tinyurl.com/techvantoacademyworkshop"
    },
    // {
    //   name: "Ethical Hacking",
    //   content: "11th,Sept 2021",
    //   delay: "600",
    //   image: "ethical.jpg",
    //   // gForm : "http://tinyurl.com/techvantoacademyworkshop"
    // },
  ];

  const classes = useStyles();
  const theme = useTheme();

  // //console.log(user);

  const [values, setValues] = useState({
    name: "",
    email: "",
    phoneNumber: "",
    workshop: workshop ? workshop.name : "Machine Learning with Python",
    college: "",
    branch: "",
    year: "",
  });
  const [isLoading, setIsLoading] = useState(false);
  const [showConfirm, setShowConfirm] = useState(false);
  const [showError, setShowError] = useState(false);

  const [registered, setRegistered] = useState(false);

  const validatePhoneNumber = (number) => {
    const isValidPhoneNumber = validator.isMobilePhone(number, ["en-IN"]);
    return isValidPhoneNumber;
  };

  const submitRegistration = () => {
    setIsLoading(true);

    // console.log(values);
    if (!validatePhoneNumber(values.phoneNumber)) {
      setShowError(true);
      setIsLoading(false);
      return;
    }
    axios
      .post(
        "https://witp-wiss.herokuapp.com/api/register/initiateRegistration",
        values
        // {
        //   withCredentials: true,
        // }
      )
      .then((response) => {
        setIsLoading(false);
        if (response.data.paid === true) setRegistered(true);
        setShowConfirm(true);

        console.log(response);
      })
      .catch((err) => {
        setIsLoading(false);
        setShowError(true);

        console.log(err);
      });
  };
  const handleChange = (event) => {
    setValues({
      ...values,
      [event.target.name]: event.target.value,
    });
  };

  const handleCloseModal = () => {
    setShowConfirm(false);
    setRegistered(false);
    handleClose();
  };

  const handleConfirm = () => {
    window.location.href = "https://pages.razorpay.com/witp";

    setShowConfirm(false);
    setRegistered(false);
    handleClose();
  };

  const handleError = () => {
    setRegistered(false);
    setShowError(false);
  };

  const alertDetails = [
    "Registration has been successful. Click on continue to pay for the program",
    "You have successfully completed the payment and are registered for the workshop. Please check your email for further updates",
  ];
  const successRegistration = (
    <div>
      <h1>You have recieved an email for further instructions</h1>
    </div>
  );

  const unSuccessfullRegistration = (
    <div>
      <h1>Please enter a valid email and try again</h1>
    </div>
  );

  const form = (
    <Container
      maxWidth="lg"
      marginTop="0rem"
      classes={{ root: classes.containerRoot }}
    >
      <Grid container direction="row" justify="center" alignItems="center">
        <Grid
          item
          xs={12}
          xl={8}
          marginBottom="3rem"
          classes={{ root: classes.gridItemRoot + " " + classes.order2 }}
        >
          <Card
            classes={{
              root: classes.cardRoot + " " + classes.cardRootSecondary,
            }}
          >
            <CardHeader
              subheader={
                <Grid
                  container
                  component={Box}
                  alignItems="center"
                  justifyContent="space-between"
                >
                  <Grid item xs="auto">
                    <Box
                      component={Typography}
                      variant="h3"
                      marginBottom="0!important"
                    >
                      Register
                    </Box>
                  </Grid>
                </Grid>
              }
              classes={{ root: classes.cardHeaderRoot }}
            ></CardHeader>
            <CardContent>
              <Box
                component={Typography}
                variant="h6"
                color={theme.palette.gray[600] + "!important"}
                paddingTop=".25rem"
                paddingBottom=".25rem"
                fontSize=".75rem!important"
                letterSpacing=".04em"
                marginBottom="1.5rem!important"
                classes={{ root: classes.typographyRootH6 }}
              >
                Details
              </Box>
              <div className={classes.plLg4}>
                <Grid container>
                  <Grid item xs={12} lg={6}>
                    <FormGroup>
                      <FormLabel>Name</FormLabel>
                      <FormControl
                        variant="filled"
                        width="100%"
                        style={{
                          marginBottom: "1rem!important",
                        }}
                        required
                      >
                        <Input
                          type="text"
                          required
                          name="name"
                          onChange={handleChange}
                          value={values.name}
                          classes={{ input: classes.searchInput }}
                        />
                      </FormControl>
                    </FormGroup>
                  </Grid>
                  <Grid item xs={12} lg={6}>
                    <FormGroup>
                      <FormLabel>Email</FormLabel>
                      <FormControl
                        variant="filled"
                        width="100%"
                        styles={{ marginBottom: "1rem!important" }}
                      >
                        <Input
                          style={{
                            paddingLeft: "0.75rem",
                            paddingRight: "0.75rem",
                            backgroundColor: "#fffff",
                          }}
                          classes={{ input: classes.searchInput }}
                          type="email"
                          value={values.email}
                          name="email"
                          onChange={handleChange}
                        />
                      </FormControl>
                    </FormGroup>
                  </Grid>
                </Grid>

                <Grid container>
                  <Grid item xs={12} lg={6}>
                    <FormGroup>
                      <FormLabel>Phone number</FormLabel>
                      <FormControl
                        variant="filled"
                        width="100%"
                        styles={{ marginBottom: "1rem!important" }}
                      >
                        <Input
                          style={{
                            paddingLeft: "0.75rem",
                            paddingRight: "0.75rem",
                          }}
                          classes={{ input: classes.searchInput }}
                          type="text"
                          value={values.phoneNumber}
                          name="phoneNumber"
                          onChange={handleChange}
                        />
                      </FormControl>
                    </FormGroup>
                  </Grid>
                </Grid>

                <Grid container>
                  <Grid item xs={12} lg={6}>
                    <FormGroup>
                      <FormLabel>Workshops</FormLabel>
                      <FormControl
                        variant="filled"
                        width="100%"
                        styles={{ marginBottom: "1rem!important" }}
                      >
                        <NativeSelect
                          id="demo-customized-select-native"
                          value={values.workshop}
                          onChange={handleChange}
                          input={<BootstrapInput />}
                          name="workshop"
                        >
                          {workshops.map((work, ind) => (
                            <option value={work.heading} id={ind}>
                              {work.heading}
                            </option>
                          ))}
                        </NativeSelect>
                      </FormControl>
                    </FormGroup>
                  </Grid>
                </Grid>
                <Grid container>
                  <Grid item xs={12} lg={6}>
                    <FormGroup>
                      <FormLabel>College</FormLabel>
                      <FormControl
                        variant="filled"
                        width="100%"
                        style={{
                          marginBottom: "1rem!important",
                        }}
                        required
                      >
                        <Input
                          type="text"
                          required
                          name="college"
                          onChange={handleChange}
                          value={values.college}
                          classes={{ input: classes.searchInput }}
                        />
                      </FormControl>
                    </FormGroup>
                  </Grid>
                </Grid>
                <Grid container>
                  <Grid item xs={12} lg={6}>
                    <FormGroup>
                      <FormLabel>Branch</FormLabel>
                      <FormControl
                        variant="filled"
                        width="100%"
                        style={{
                          marginBottom: "1rem!important",
                        }}
                        required
                      >
                        <Input
                          type="text"
                          required
                          name="branch"
                          onChange={handleChange}
                          value={values.branch}
                          classes={{ input: classes.searchInput }}
                        />
                      </FormControl>
                    </FormGroup>
                  </Grid>
                </Grid>
              </div>
              <Box
                component={Divider}
                marginBottom="0.8rem!important"
                marginTop="0.8rem!important"
              />
              {/* <Box
                component={Typography}
                variant="h6"
                color={theme.palette.gray[600] + "!important"}
                paddingTop=".25rem"
                paddingBottom=".25rem"
                fontSize=".75rem!important"
                letterSpacing=".04em"
                marginBottom="1.5rem!important"
                classes={{ root: classes.typographyRootH6 }}
              >
                Teacher Information
              </Box> */}
            </CardContent>

            <Box
              sx={{
                display: "center",

                p: 2,
              }}
            >
              <Button
                color="primary"
                style={{
                  marginLeft: "40%",
                  marginRight: "40%",
                  marginTop: "2%",
                  marginBottom: "2%",
                }}
                onClick={submitRegistration}
                classes={{ root: classes.buttonRootDark }}
                variant="contained"
                disabled={
                  !(
                    values.name &&
                    values.email &&
                    values.workshop &&
                    values.phoneNumber &&
                    !isLoading
                  )
                }
              >
                {isLoading ? "Loading..." : "Register"}
              </Button>
            </Box>
          </Card>
        </Grid>
      </Grid>
      <AlertDialog
        show={showConfirm}
        handleClose={handleConfirm}
        content={registered ? alertDetails[1] : alertDetails[0]}
      ></AlertDialog>
      <AlertDialog
        show={showError}
        handleClose={handleError}
        content="Please enter a valid email and mobile number for registration"
      ></AlertDialog>
    </Container>
  );

  return (
    <>
      <div className="page-wrapper bg-gra-02 p-t-130 p-b-100 font-poppins">
        <div className="wrapper wrapper--w680">
          <div className="card card-4">
            <div className="card-body">
              <h2 className="title" style={{ marginTop: "20px" }}>
                Registration Form
              </h2>
              <form method="POST">
                <div className="row row-space">
                  <div className="col-2">
                    <div className="input-group">
                      <label className="label">Name</label>
                      <input
                        className="input--style-4"
                        type="text"
                        name="name"
                        value={values.name}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                  <div className="col-2">
                    <div className="input-group">
                      <label className="label">Phone Number</label>
                      <input
                        className="input--style-4"
                        type="text"
                        name="phoneNumber"
                        value={values.phoneNumber}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                </div>

                <div className="row row-space">
                  <div className="col-2">
                    <div className="input-group">
                      <label className="label">Email</label>
                      <input
                        className="input--style-4"
                        type="email"
                        name="email"
                        value={values.email}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                  <div className="col-2">
                    <div className="input-group">
                      <label className="label">College</label>
                      <input
                        className="input--style-4"
                        type="text"
                        name="college"
                        value={values.college}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                </div>
                <div className="row row-space">
                  <div className="col-2">
                    <div className="input-group">
                      <label className="label">Branch</label>
                      <input
                        className="input--style-4"
                        type="text"
                        name="branch"
                        value={values.branch}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                  <div className="col-2">
                    <div className="input-group">
                      <label className="label">Studying year</label>
                      <input
                        className="input--style-4"
                        type="text"
                        name="year"
                        value={values.year}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                </div>
                <div>
                  <div className="row row-space">
                    <div className="col-2">
                      <div className="input-group">
                        <label className="label">Select workshop</label>
                        <select
                          name="workshop"
                          onChange={handleChange}
                          value={values.workshop}
                          className="input--style-4"
                          style={{ padding: "10px", border: "none" }}
                        >
                          {workshops.map((workshop, index) => (
                            <option value={workshop.name} key={index}>
                              {workshop.name}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                  </div>
                </div>
                {isLoading ? <div className="loader">Loading...</div> : null}
                <div
                  className="testimonial-item-footer text-xs mt-32 mb-0 has-top-divider"
                  style={{
                    position: "relative",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  <span className="testimonial-item-link">
                    <Button
                      tag="a"
                      color="dark"
                      wideMobile
                      disabled={
                        !values.name ||
                        !values.email ||
                        !values.phoneNumber ||
                        !values.year ||
                        !values.college ||
                        !values.branch
                      }
                      onClick={submitRegistration}
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      Register
                    </Button>
                  </span>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <AlertDialog
        show={showConfirm}
        handleClose={handleCloseModal}
        confirm={true}
        handleConfirm={handleConfirm}
        content={registered ? alertDetails[1] : alertDetails[0]}
      ></AlertDialog>
      <AlertDialog
        show={showError}
        handleClose={handleError}
        content="Please enter a valid email and mobile number for registration"
      ></AlertDialog>
    </>
  );
}

export default Register;
